@import "_variables.scss";
@import "_mixins.scss";
@import "_animations.scss";

.Timer {
  @include flex-center;
  bottom: 0;
  left: 0;
  position: fixed;
  right: 0;
  top: 0;
  transition: visibility 0s $time-slide;
  visibility: hidden;

  &--visible {
    transition: visibility 0s;
    visibility: visible;
  }

  &__container {
    align-items: center;
    column-gap: 20px;
    display: grid;
    flex-grow: 1;
    font-size: 1.6em;
    font-weight: 600;
    grid-template-columns: 300px 1fr 300px;
    grid-template-rows: 1fr 80px 1fr;
    height: 100%;
    justify-items: center;
    max-width: 1200px;
    row-gap: 100px;
    width: 100%;
  }

  &__heading {
    margin: 0;
    font-size: 2em;
    font-weight: 400;
    grid-column: 1 / span 3;
  }

  &__display {
    color: $color-tomato;
    font-family: 'Inconsolata';
    font-size: 3em;
    font-weight: 700;
    flex-direction: column;
    height: 100%;
    position: relative;
    width: 100%;

    &--inactive {
      color: #fff;
    }
  }
}